<template>
    <div>
        <table class="table">
            <thead class="head">
                <th class="head__tHead">S.No</th>
                <th class="head__tHead">Name</th>
                <th class="head__tHead">see user data</th>
            </thead>
            <tbody class="body">
                <tr v-for="user in userData" :key="user.id" class="body__tr">
                    <td class="body__tr__tData">{{ user.id }}</td>
                    <td class="body__tr__tData">{{ user.name }}</td>
                    <td class="body__tr__tData">
                        <button class="body__tr__tData__button" @click="handleClick(user)">Check Details</button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import axios from 'axios'

import router from '../router/index'

interface UserData {
    id: number,
    name: string,
    username: string,
    email: string,
    address: {
        street: string,
        suite: string,
        city: string,
        zipcode: string,
        geo: {
            lat: string,
            lng: string,
        },
    },
    website: string,
    phone: string,
    company: {
        name: string,
        catchPhrase: string,
        bs: string,
    },
}

const userData = ref<UserData[]>();

const handleClick = (user: UserData) => {
    router.replace({ name: 'userDetails', params: { userData: user } });
}

onMounted(() => {
    axios({
        method: 'get',
        url: 'https://jsonplaceholder.typicode.com/users'
    })
        .then(response => userData.value = response.data);
})
</script>

